import React from 'react';
import * as firebase from 'firebase';

export class Login extends React.Component{
constructor(props){
    super(props);
    this.state = {message : ''};
    

}
add(){
    var userid = this.refs.userid.value;
    var pwd = this.refs.pwd.value;
    var promise = firebase.database().ref('users/'+userid).set({'userid':userid,'pwd':pwd});
    promise.then(data=>{
        this.setState({message:'User register SuccessFully'});
    }).catch(err=>{
        this.setState({message:"Can't Register Some Problem"+err});
    })
}
checkLogin(){
    //console.log("CheckLogin Called...",this.refs.userid.value);
    var userid= this.refs.userid.value;
    var pwd =this.refs.pwd.value;
    var users = firebase.database().ref('users/'+userid);
    users.on('value',(snapshot)=>{
        var userObject = snapshot.val();
        console.log("Get the UserObject ",userObject);
        if(userObject.pwd==pwd){
            this.props.updateLoginStatus(true, userid);
        }
        else{
            this.setState({message:'Invalid Userid or Password'});
        }
    })
    
   
    /*if(userid==pwd){
        this.props.updateLoginStatus(true, userid);
    }
    else{
        this.setState({message:'Invalid Userid or Password'});
    }*/
    
}
render(){
    return(
    <div>
        <h1>Login Page</h1>
        <h3 className="alert-danger">{this.state.message}</h3>
        <div className='form-group'>
            <label htmlFor=""></label>
            <input type="text" className='form-control' ref='userid' placeholder='Type Userid Here '/>
        </div>
        <div className='form-group'>
            <label htmlFor=""></label>
            <input type="password" className='form-control' ref='pwd' placeholder='Type Password Here '/>
        </div>
        <div className='form-group'>
            <button onClick={this.checkLogin.bind(this)} className='btn btn-primary'>Login</button>&nbsp;
            <button className='btn btn-success' onClick={this.add.bind(this)}>Register</button>
            <button className='btn btn-dark'>Clear All</button>
        </div>

    </div>
    );
}
}