import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import {Header} from './components/Header';
import { Shop } from './containers/shop';
import {Nav} from './components/nav';
import {AboutUs} from './components/AboutUs';
import {Switch,Route} from 'react-router-dom';
import {Search} from './components/Search';
import {Sort} from './components/Sort';
import {Login} from './components/Login';

class App extends Component {
  constructor(props){
    super(props);
    this.state = {isLogin:false,userName:''};
    
  }
  isAuth(loginStatus,uid){
    this.setState({isLogin:loginStatus,userName:uid});
  }
  render() {
    var jsx = (<div><Header/>
    <Nav/>
    <div className="row"> 
      <div className="col-sm-6">
      <Search/>
      </div>
      <div className="col-sm-6">
      <Sort/>
      </div>
    
    </div>

    <Switch>
    
    <Route path={`${process.env.PUBLIC_URL}/`} exact component={Shop}/>
    <Route path={`${process.env.PUBLIC_URL}/about`}  exact component={AboutUs}/>
    </Switch>
    </div>
    );
    
   
    return (
      <div className='container'> 
        
      {this.state.isLogin?jsx:<Login updateLoginStatus={this.isAuth.bind(this)}/>}
      </div>

    );
  }
}

export default App;
