import React from 'react';
import * as firebase from 'firebase';
export class AboutUs extends React.Component{
    constructor(props){
        super(props);
        this.state = {message:''};
    }
    add(){
        var id = this.refs.id.value;
        var name = this.refs.name.value;
        var price = this.refs.price.value;
        var url = this.refs.url.value;
        var itemObject = {"id":id,"name":name,"price":price,"url":url};
        var promise = firebase.database().ref('products/'+id).set(itemObject);
        promise.then(data=>{
            this.setState({message:'Record Added'});
        }).catch(err=>{
            this.setState({message:'Error in Record Addition'});
            console.log("Add Error is ",err);
        })
    }
    render(){
        return (
            <div>
                <h2 className="alert-success">Add Item Section</h2>
                <h4 className="alert-info">{this.state.message}</h4>
                <div className="form-group">
                    <label htmlFor="">Id</label>
                    <input type="text" className="form-control" placeholder="Type Id Here" ref="id"/>
                </div>
                <div className="form-group">
                    <label htmlFor="">Name</label>
                    <input type="text" className="form-control" placeholder="Type Name Here" ref="name"/>
                </div>
                <div className="form-group">
                    <label htmlFor="">Price</label>
                    <input type="text" className="form-control" placeholder="Type Price Here" ref="price"/>
                </div>
                <div className="form-group">
                    <label htmlFor="">URL</label>
                    <input type="text" className="form-control" placeholder="Type URL Here" ref="url"/>
                </div>
                <div className="form-group">
                   <button onClick={this.add.bind(this)} className="btn btn-primary">Add Item</button>
                   
                </div>
            </div>
        )
    }
}