import React from 'react';
import {Products} from '../components/products';
import {Config} from '../utils/config';
import {itemOperations} from '../models/itemoperations';
import * as firebase from 'firebase';
export class Shop extends React.Component{
    constructor(props){
        super(props);
        this.state = {"items":[]};
        console.log("Constructor Call ",this.state.items);
    }
    componentDidMount () {
        var items = [];
        var prodRef = firebase.database().ref('products');
        prodRef.on("value",(snapshot)=>{
            //console.log("Snap Shot is ::: ",snapshot.val());
            var object = snapshot.val();
            for(let key in object){
                items.push(object[key]);
            }
            this.setState({"items":items});
        })
        /*console.log("Component Did Mount Call",this.state.items);
        fetch(Config.url).then(response=>{
            response.json().then(data=>{
                itemOperations.items = data['mobiles'];
                console.log("Items are::::: ",data['mobiles']);
                this.setState({"items":itemOperations.items});
                
            })
        })*/
    }
    

    render(){
        return(
            <Products items={this.state.items}/>
        )
    }
}